import { combineReducers } from 'redux';
import { all } from 'redux-saga/effects';
import auth, { authSaga } from './auth';
import loading from './loading';
import user, { userSaga } from './user';
import write, { writeSaga } from './write';
import list, { listSaga } from './list';
import minilist, { minilistSaga } from './minilist';
import post, { postSaga } from './post';
import update, { updateSaga } from './update';
import comment, { commentSaga } from './comment';
import recomment, { recommentSaga } from './recomment';

import auths, { authsSaga } from './auths';
import users, { usersSaga } from './users';
import profile, { profileSaga } from './profile';
import myprofile, { myprofileSaga } from './myprofile';
import guestbook, { guestbookSaga } from './guestbook';
import updateprofile, { updateprofileSaga } from './updateprofile';
import rank, { rankpostSaga } from './rank';
import award, { awardpostSaga } from './award';

import minipost, { minipostSaga } from './minipost';
import opener from './opener';
import openpost from './openpost';
import position from './position';

const rootReducer = combineReducers({
  auth,
  loading,
  user,
  write,
  list,
  post,
  update,
  comment,
  recomment,

  guestbook,
  auths,
  users,

  profile,
  myprofile,
  updateprofile,
  rank,
  award,
  minilist,
  minipost,
  opener,
  openpost,
  position,
});

export function* rootSaga() {
  yield all([
    authSaga(),
    userSaga(),
    writeSaga(),
    listSaga(),
    postSaga(),
    updateSaga(),
    commentSaga(),

    authsSaga(),
    usersSaga(),
    profileSaga(),
    guestbookSaga(),
    updateprofileSaga(),

    rankpostSaga(),
    awardpostSaga(),
    recommentSaga(),
    myprofileSaga(),
    minilistSaga(),
    minipostSaga(),
  ]);
}

export default rootReducer;
