import React, { useEffect, useState, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';

import { createComment } from '../../modules/comment';
import CommentItem from '../../components/comment/CommentItem';
// import Button from '@material-ui/core/Button';
import Button from '../../components/common/BlueButton';
import client from '../../lib/api/client';
import styled from 'styled-components';

const TitleInput = styled.textarea`
  font-size: 1rem;
  outline: none;
  padding: 1rem;
  margin-bottom: 1rem;
  width: 100%;
  box-sizing: border-box;
  border: solid 2px white;
  border-image-slice: 1;
  background: none;
  border-color: white;
  color: white;
`;

const CommentContainer = ({ postId, key }) => {
  const [commentall, setCommentall] = useState(null);
  const postid = parseInt(postId);
  const commenter = parseInt(localStorage.getItem('user_id'));
  const dispatch = useDispatch();
  const name = localStorage.getItem('current_user');
  const [text, setText] = useState('');

  const getCommentall = (postid) => {
    client
      .get(`/api/posts/${postid}`)
      .then((res) => setCommentall(res.data.comments))
      .catch((e) => {
        return e;
      });
  };
  useEffect(() => {
    getCommentall(postid);
  }, [postid]);

  const onClick = useCallback(async () => {
    await getCommentall(postid);
  }, [postid]);
  const onCreate = useCallback(async () => {
    await dispatch(createComment({ commenter, name, text, postid }));
    await setText('');
    await getCommentall(postid);
  }, [text, setText, postid, commenter, name, dispatch]);
  const onChange = useCallback((e) => {
    setText(e.target.value);
  }, []);
  if (commentall) {
    return (
      <div
        style={{
          background: 'rgba(255, 255, 255, 0.2)',
        }}
      >
        <div
          style={{
            padding: '20px',
          }}
        >
          <TitleInput
            id="outlined-multiline-static"
            label="댓글"
            multiline
            rows="4"
            fullWidth
            variant="outlined"
            value={text}
            onChange={onChange}
          />
          <span onClick={onCreate}>
            <Button onClick={onClick} variant="outlined" color="secondary">
              등록
            </Button>
          </span>
        </div>

        {commentall.map((comment, index) => (
          <>
            <CommentItem
              commenterId={comment.commenter}
              name={comment.name}
              text={comment.text}
              commentId={comment.id}
              created_at={comment.created_at}
              onDelete={onClick}
              key={index}
              comment={comment}
              setCommentall={setCommentall}
              postid={postid}
            />
          </>
        ))}
      </div>
    );
  } else {
    return <></>;
  }
};

export default withRouter(CommentContainer);
