import { createAction, handleActions } from 'redux-actions';
import createRequestSaga, {
  createRequestActionTypes,
} from '../lib/createRequestSaga';
import * as authAPI from '../lib/api/auth';
import { takeLatest } from 'redux-saga/effects';

const [
  RANK_POST,
  RANK_POST_SUCCESS,
  RANK_POST_FAILURE,
] = createRequestActionTypes('rank/RANK_POST');
const CLEAR = createRequestActionTypes('rank/CREAR');
export const rankPosts = createAction(RANK_POST);
export const clear = createAction(CLEAR);

export function* rankpostSaga() {
  yield takeLatest(RANK_POST, createRequestSaga(RANK_POST, authAPI.rankPosts));
}

const initialState = {
  rank: null,
  error: null,
};

const rank = handleActions(
  {
    [RANK_POST_SUCCESS]: (state, { payload: rank }) => ({
      ...state,
      rank,
    }),
    [RANK_POST_FAILURE]: (state, { payload: error }) => ({
      ...state,
      error,
    }),
    [CLEAR]: state => initialState,
  },
  initialState,
);

export default rank;
