import { createAction, handleActions } from 'redux-actions';
import createRequestSaga, {
  createRequestActionTypes,
} from '../lib/createRequestSaga';
import * as postsAPI from '../lib/api/posts';
import { takeLatest } from 'redux-saga/effects';

const INITIALIZE = 'recomment/INITIALIZE';
const CHANGE_FIELD = 'recomment/CHANGE_FIELD';

const [
  CREATE_RECOMMENT,
  CREATE_RECOMMENT_SUCCESS,
  CREATE_RECOMMENT_FAILURE,
] = createRequestActionTypes('create/CREATE_RECOMMENT');

export const initialize = createAction(INITIALIZE);
export const changeRecomment = createAction(
  CHANGE_FIELD,
  ({ commenter, name, text, commentid }) => ({
    commenter,
    name,
    text,
    commentid,
  }),
);
export const createRecomment = createAction(
  CREATE_RECOMMENT,
  ({ commenter, name, text, commentid }) => ({
    commenter,
    name,
    text,
    commentid,
  }),
);

const createRecommentSaga = createRequestSaga(
  CREATE_RECOMMENT,
  postsAPI.createRecomment,
);
export function* recommentSaga() {
  yield takeLatest(CREATE_RECOMMENT, createRecommentSaga);
}

const initialState = {
  commenter: '',
  name: '',
  commentid: '',
  text: '',
  recomment: null,
  recommentError: null,
};

const recomment = handleActions(
  {
    [INITIALIZE]: state => initialState,
    [CHANGE_FIELD]: (
      state,
      { payload: { commenter, name, text, commentid } },
    ) => ({
      ...state,
      commenter: commenter,
      commentid: commentid,
      text: text,
      name: name,
    }),
    [CREATE_RECOMMENT]: state => ({
      ...state,
      recomment: null,
      recommentError: null,
    }),
    [CREATE_RECOMMENT_SUCCESS]: (state, { payload: recomment }) => ({
      ...state,
      recomment,
    }),
    [CREATE_RECOMMENT_FAILURE]: (state, { payload: recommentError }) => ({
      ...state,
      recommentError,
    }),
  },
  initialState,
);

export default recomment;
