import React, { useEffect, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { writePost, changeField } from '../../modules/write';
import WriteActionButtons from '../../components/writecategory/WriteActionButtons';

const WriteActionButtonsContainer = ({ history, match }) => {
  const dispatch = useDispatch();
  const {
    title,
    YouTube,
    content,
    username,
    profileid,
    post,
    postError,
    category,
    image_file,
  } = useSelector(({ write }) => ({
    title: write.title,
    content: write.content,
    YouTube: write.YouTube,
    username: write.username,
    profileid: write.profileid,
    post: write.post,
    portError: write.postError,
    category: write.category,
    image_file: write.image_file,
  }));

  const onPublish = () => {
    if (image_file === '') {
      dispatch(
        writePost({
          title,
          YouTube,
          content,
          username,
          profileid,
          category,
          image_file,
        }),
      );
    } else {
      dispatch(
        writePost({
          title,
          YouTube,
          content,
          username,
          profileid,
          category,
          image_file,
        }),
      );
    }
  };
  const onChangeField = useCallback(
    (payload) => dispatch(changeField(payload)),
    [dispatch],
  );
  useEffect(() => {
    onChangeField({ key: 'category', value: 'gallery' });
  }, [onChangeField]);

  const onCancel = () => {
    history.goBack();
  };

  useEffect(() => {
    if (post) {
      const { id } = post;
      history.push(`/post/${id}`);
    }
    if (postError) {
      console.log(postError);
    }
  }, [history, post, postError]);

  return <WriteActionButtons onPublish={onPublish} onCancel={onCancel} />;
};

export default withRouter(WriteActionButtonsContainer);
