import { createAction, handleActions } from 'redux-actions';
import createRequestSaga, {
  createRequestActionTypes,
} from '../lib/createRequestSaga';
import * as postsAPI from '../lib/api/posts';
import { takeLatest } from 'redux-saga/effects';

const [
  AWARD_POST,
  AWARD_POST_SUCCESS,
  AWARD_POST_FAILURE,
] = createRequestActionTypes('award/AWARD_POST');
const CLEAR = createRequestActionTypes('award/CREAR');
export const awardPosts = createAction(AWARD_POST);

export const clear = createAction(CLEAR);

export function* awardpostSaga() {
  yield takeLatest(
    AWARD_POST,
    createRequestSaga(AWARD_POST, postsAPI.awardPosts),
  );
}

const initialState = {
  award: null,
  error: null,
};

const award = handleActions(
  {
    [AWARD_POST_SUCCESS]: (state, { payload: award }) => ({
      ...state,
      award,
    }),
    [AWARD_POST_FAILURE]: (state, { payload: error }) => ({
      ...state,
      error,
    }),
    [CLEAR]: state => initialState,
  },
  initialState,
);

export default award;
