import React, { useCallback, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Popover from '@material-ui/core/Popover';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import client from '../../lib/api/client';
import Avatar from '@material-ui/core/Avatar';

const useStyles = makeStyles((theme) => ({
  typography: {
    padding: theme.spacing(2),
  },
}));

const FollowItem = ({ followid }) => {
  const [followData, setFollowData] = useState(null);

  const getData = useCallback((followid) => {
    client
      .get(`/api/miniprofile/${followid}`)
      .then((res) => setFollowData(res.data))
      .catch((e) => {
        return e;
      });
  }, []);
  getData(followid);
  if (followData) {
    const user_image = followData.user_image;
    const nickname = followData.nickname;
    const userid = followData.user;
    return (
      <>
        <Button href={`/profile/${userid}`}>
          <h3>{nickname}</h3>
          {user_image && <Avatar src={user_image} />}
        </Button>
      </>
    );
  } else {
    return <></>;
  }
};

export default function FollowerPopover({ type, num, follow }) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;
  return (
    <div>
      <Button aria-describedby={id} onClick={handleClick}>
        {type} : {num}
      </Button>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
        <Typography className={classes.typography}>
          {follow && follow.map((num) => <FollowItem followid={num} />)}
        </Typography>
      </Popover>
    </div>
  );
}
