import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import styled from 'styled-components';
import BlueButton from '../common/BlueButton';

const IdInput = styled.input`
  font-size: 1rem;
  outline: none;
  padding: 1rem;
  margin-bottom: 1rem;
  width: 100%;
  box-sizing: border-box;
  border-style: solid;
  border-image-slice: 1;
  background: none;
  border-color: white;
  color: white;
`;
const PasswordInput = styled.input`
  font-size: 1rem;
  outline: none;
  padding: 1rem;
  margin-bottom: 1rem;
  width: 100%;
  box-sizing: border-box;
  border-style: solid;
  border-image-slice: 1;
  background: none;
  border-color: white;
  color: white;
`;
const ErrorMessage = styled.div`
  color: red;
  text-align: center;
  font-size: 0.875rem;
  margin-top: 1rem;
`;

const textMap = {
  login: 'LOGIN',
  register: 'REGISTER',
};

const useStyles = makeStyles((theme) => ({
  paper: {
    // marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },

  form: {
    width: '100%', // Fix IE 11 issue.
    // marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(0, 0, 2),
  },
}));

export default function AuthForm({ type, form, onChange, onSubmit, error }) {
  const classes = useStyles();
  const text = textMap[type];
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <h1
          style={{
            margin: 0,
            padding: 0,
            fontSize: '30px',
            color: 'white',
            fontWeight: 800,
          }}
        >
          {text}
        </h1>
        <form className={classes.form} noValidate onSubmit={onSubmit}>
          <IdInput
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="username"
            placeholder="ID"
            name="username"
            autoComplete="username"
            onChange={onChange}
            value={form.username}
            autoFocus
          />
          {type === 'register' && (
            <IdInput
              variant="outlined"
              required
              fullWidth
              id="email"
              placeholder="Email Address"
              name="email"
              autoComplete="username"
              onChange={onChange}
              value={form.email}
            />
          )}
          <PasswordInput
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            placeholder="Password"
            type="password"
            autoComplete="new-password"
            onChange={onChange}
            value={form.password}
          />
          {type === 'register' && (
            <PasswordInput
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="passwordConfirm"
              placeholder="new-Password"
              type="password"
              id="password"
              autoComplete="new-password"
              onChange={onChange}
              value={form.passwordConfirm}
            />
          )}
          {error && (
            <ErrorMessage>
              요청에 실패했습니다. 올바르게 입력해 주세요
            </ErrorMessage>
          )}
          <BlueButton
            type="submit"
            fullWidth
            variant="contained"
            color="secondary"
            className={classes.submit}
          >
            {text}
          </BlueButton>
          {/* <GoogleForm /> */}
        </form>
      </div>
    </Container>
  );
}
