import { createAction, handleActions } from 'redux-actions';
import createRequestSaga, {
  createRequestActionTypes,
} from '../lib/createRequestSaga';
import * as authAPI from '../lib/api/auth';
import { takeLatest } from 'redux-saga/effects';

const INITIALIZE = 'updateprofile/INITIALIZE';
const CHANGE_FIELD = 'updateprofile/CHANGE_FIELD';

//수정 부분
const SET_ORIGINAL_PROFILE = 'updateprofile/SET_ORIGINAL_PROFILE';
const [
  UPDATE_PROFILE,
  UPDATE_PROFILE_SUCCESS,
  UPDATE_PROFILE_FAILURE,
] = createRequestActionTypes('updateprofile/UPDATE_PROFILE');

export const updateProfile = createAction(
  UPDATE_PROFILE,
  ({ user, nickname, description, user_image }) => ({
    user,
    nickname,
    description,
    user_image,
  }),
);

export const setOriginalProfile = createAction(
  SET_ORIGINAL_PROFILE,
  profile => profile,
);
export const initialize = createAction(INITIALIZE);
export const changeField = createAction(CHANGE_FIELD, ({ key, value }) => ({
  key,
  value,
}));

export function* updateprofileSaga() {
  yield takeLatest(
    UPDATE_PROFILE,
    createRequestSaga(UPDATE_PROFILE, authAPI.updateProfile),
  );
}

const initialState = {
  nickname: '',
  description: '',
  user_image: '',
  profile: null,
  profileError: null,
};

const updateprofile = handleActions(
  {
    [INITIALIZE]: state => initialState,
    [CHANGE_FIELD]: (state, { payload: { key, value } }) => ({
      ...state,
      [key]: value,
    }),
    // 수정부분
    [SET_ORIGINAL_PROFILE]: (state, { payload: profile }) => ({
      ...state,
      nickname: profile.nickname,
      description: profile.description,
      user_image: profile.user_image,
    }),
    [UPDATE_PROFILE_SUCCESS]: (state, { payload: profile }) => ({
      ...state,
      profile,
    }),
    [UPDATE_PROFILE_FAILURE]: (state, { payload: postError }) => ({
      ...state,
      postError,
    }),
  },
  initialState,
);

export default updateprofile;
