import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { readProfile, unloadProfile } from '../../modules/profile';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import FollowButton from '../../components/profile/FollowButton';

const ProfilecardContainer = ({ userId, followtrue }) => {
  const dispatch = useDispatch();
  const { profile } = useSelector(({ profile }) => ({
    profile: profile.profile,
  }));

  useEffect(() => {
    dispatch(readProfile(userId));
    return () => {
      dispatch(unloadProfile());
    };
  }, [dispatch, userId]);

  if (profile) {
    return (
      <>
        <Button href={`/profile/${profile.user}`}>
          <h3 style={{ color: 'white' }}>{profile.nickname}</h3>
          {profile.user_image && <Avatar src={profile.user_image} />}
        </Button>
        {followtrue && (
          <FollowButton
            own_follower={profile.follower}
            own_following={profile.following}
            own_id={profile.user}
          />
        )}
      </>
    );
  } else {
    return <></>;
  }
};

export default withRouter(ProfilecardContainer);
