import { createAction, handleActions } from 'redux-actions';
import createRequestSaga, {
  createRequestActionTypes,
} from '../lib/createRequestSaga';
import * as postsAPI from '../lib/api/posts';
import { takeLatest } from 'redux-saga/effects';

const INITIALIZE = 'update/INITIALIZE';
const CHANGE_FIELD = 'update/CHANGE_FIELD';

//수정 부분
const SET_ORIGINAL_POST = 'update/SET_ORIGINAL_POST';
const [
  UPDATE_POST,
  UPDATE_POST_SUCCESS,
  UPDATE_POST_FAILURE,
] = createRequestActionTypes('update/UPDATE_POST');

export const updatePost = createAction(
  UPDATE_POST,
  ({ id, title, content }) => ({ id, title, content }),
);

export const setOriginalPost = createAction(SET_ORIGINAL_POST, post => post);
export const initialize = createAction(INITIALIZE);
export const changeField = createAction(CHANGE_FIELD, ({ key, value }) => ({
  key,
  value,
}));

export function* updateSaga() {
  yield takeLatest(
    UPDATE_POST,
    createRequestSaga(UPDATE_POST, postsAPI.updatePost),
  );
}

const initialState = {
  title: '',
  content: '',
  post: null,
  postError: null,
};

const update = handleActions(
  {
    [INITIALIZE]: state => initialState,
    [CHANGE_FIELD]: (state, { payload: { key, value } }) => ({
      ...state,
      [key]: value,
    }),
    // 수정부분
    [SET_ORIGINAL_POST]: (state, { payload: post }) => ({
      ...state,
      title: post.title,
      content: post.content,
    }),
    [UPDATE_POST_SUCCESS]: (state, { payload: post }) => ({
      ...state,
      post,
    }),
    [UPDATE_POST_FAILURE]: (state, { payload: postError }) => ({
      ...state,
      postError,
    }),
  },
  initialState,
);

export default update;
