import { createAction, handleActions } from 'redux-actions';
import createRequestSaga, {
  createRequestActionTypes,
} from '../lib/createRequestSaga';
import * as postsAPI from '../lib/api/posts';
import { takeLatest } from 'redux-saga/effects';

const INITIALIZE = 'comment/INITIALIZE';
const CHANGE_FIELD = 'comment/CHANGE_FIELD';

const [
  CREATE_COMMENT,
  CREATE_COMMENT_SUCCESS,
  CREATE_COMMENT_FAILURE,
] = createRequestActionTypes('create/CREATE_COMMENT');

export const initialize = createAction(INITIALIZE);
export const changeComment = createAction(
  CHANGE_FIELD,
  ({ commenter, name, text, postid }) => ({
    commenter,
    name,
    text,
    postid,
  }),
);
export const createComment = createAction(
  CREATE_COMMENT,
  ({ commenter, name, text, postid }) => ({ commenter, name, text, postid }),
);

const createCommentSaga = createRequestSaga(
  CREATE_COMMENT,
  postsAPI.createComment,
);
export function* commentSaga() {
  yield takeLatest(CREATE_COMMENT, createCommentSaga);
}

const initialState = {
  commenter: '',
  name: '',
  postid: '',
  text: '',
  comment: null,
  commentError: null,
  recomments: null,
};

const comment = handleActions(
  {
    [INITIALIZE]: state => initialState,
    [CHANGE_FIELD]: (
      state,
      { payload: { commenter, name, text, postid } },
    ) => ({
      ...state,
      commenter: commenter,
      postid: postid,
      text: text,
      name: name,
    }),
    [CREATE_COMMENT]: state => ({
      ...state,
      comment: null,
      commentError: null,
    }),
    [CREATE_COMMENT_SUCCESS]: (state, { payload: comment }) => ({
      ...state,
      comment,
      recomments: comment.recomments,
    }),
    [CREATE_COMMENT_FAILURE]: (state, { payload: commentError }) => ({
      ...state,
      commentError,
    }),
  },
  initialState,
);

export default comment;
