import React, { useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { awardPosts } from '../../modules/award';
import Awardtable from '../../components/award/Awardtable';
import { SmallHeader } from '../../components/common/SmallHeader';

const AwardContainer = () => {
  const dispatch = useDispatch();
  const { award } = useSelector(({ award }) => ({
    award: award.award,
  }));

  useEffect(() => {
    dispatch(awardPosts());
  }, [dispatch]);

  if (!award) {
    return <></>;
  }
  return (
    <>
      <SmallHeader>
        <h1 className="title">어워드</h1>
        <hr />
        <h1 className="description">
          많은 사람에게 좋아요를 받은 작품입니다. 여러분도 도전해보세요
        </h1>
      </SmallHeader>
      <Awardtable award={award} />
    </>
  );
};

export default withRouter(AwardContainer);
