import React from 'react';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import { SmallHeader } from '../common/SmallHeader';
import { S3_url } from '../../lib/api/server_url';

const DefaultItem = (num) => {
  const n = num.num;
  return (
    <div
      style={{
        width: '300px',
        height: '350px',
        overflow: 'hidden',
        margin: '20px',
      }}
    >
      <Link to={S3_url + `default/1+(${n}).jpg`} target="_blank" download>
        <img src={S3_url + `default/1+(${n}).jpg`} alt="default" />
      </Link>
    </div>
  );
};

const Default = () => {
  const number = [];
  for (let i = 1; i <= 44; i++) {
    number.push(i);
  }

  return (
    <>
      <SmallHeader>
        <h1 className="title">밑그림</h1>
        <hr />
        <h1 className="description">
          밑그림을 다운받아 새로운 그림을 색칠해 보세요. 클릭하면
          다운로드됩니다.
        </h1>
      </SmallHeader>

      <Grid container>
        {number.map((num, index) => (
          <Grid item>
            <DefaultItem num={num} key={index} />
          </Grid>
        ))}
      </Grid>
    </>
  );
};

export default Default;
