import React, { useRef, useEffect, useState } from 'react';
import Quill from 'quill';
import 'quill/dist/quill.bubble.css';
import styled from 'styled-components';

const TitleInput = styled.input`
  font-size: 1rem;
  outline: none;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
  width: 100%;
  box-sizing: border-box;
  border-style: solid;
  border-image-slice: 1;
  background: none;
  border-color: white;
  color: white;
`;

const QuillWrapper = styled.div`
  box-sizing: border-box;
  border-style: solid;

  border-image-slice: 1;
  color: white;
  .ql-editor {
    padding: 0;
    min-height: 320px;
    font-size: 1rem;
    line-height: 1.5;
  }
  .ql-editor.ql-blank::before {
    left: 0px;
    font-style: normal;
    color: rgb(31, 31, 36);
  }
`;

const EditorWrapper = styled.div`
  color: white;
`;

const QuillItem = ({ onChangeField }) => {
  const quillElement = useRef(null);
  const quillInstance = useRef(null);
  useEffect(() => {
    quillInstance.current = new Quill(quillElement.current, {
      theme: 'bubble',
      modules: {
        toolbar: [
          [{ header: '1' }, { header: '2' }],
          ['bold', 'italic', 'underline', 'strike'],
          [{ list: 'ordered' }, { list: 'bullet' }],
          ['blockquote', 'code-block', 'link', 'image'],
        ],
      },
    });
    const quill = quillInstance.current;
    quill.on('text-change', (delta, oldDelta, source) => {
      if (source === 'user') {
        onChangeField({ key: 'content', value: quill.root.innerHTML });
      }
    });
  }, [onChangeField]);

  return (
    <>
      <p>컨텐츠</p>
      <QuillWrapper ref={quillElement} />
    </>
  );
};

const Editor = ({ title, onChangeField }) => {
  const onChangeTitle = (e) => {
    onChangeField({ key: 'title', value: e.target.value });
  };
  useEffect(() => {
    onChangeField({
      key: 'username',
      value: localStorage.getItem('current_user'),
    });
    onChangeField({
      key: 'profileid',
      value: localStorage.getItem('user_id'),
    });
  }, [onChangeField]);

  return (
    <EditorWrapper>
      <p>제목</p>
      <TitleInput onChange={onChangeTitle} value={title} />
      <ImgUpload onChangeField={onChangeField} />
      <p>내용</p>
      <QuillItem onChangeField={onChangeField} />
    </EditorWrapper>
  );
};

export default Editor;

const ImgUpload = ({ onChangeField }) => {
  const [imgBase64, setImgBase64] = useState('');
  const [imgFile, setImgFile] = useState(null);
  const [isBig, setIsBig] = useState(false);

  const handleChangeFile = (event) => {
    let reader = new FileReader();
    reader.onloadend = (e) => {
      const base64 = reader.result;
      if (base64) {
        if (base64.length <= 5 * 1024 * 1024) {
          setIsBig(false);
          setImgBase64(base64.toString());
          onChangeField({ key: 'image_file', value: base64.toString() });
        } else {
          setIsBig(true);
        }
      }
    };
    if (event.target.files[0]) {
      reader.readAsDataURL(event.target.files[0]);
      setImgFile(event.target.files[0]);
    }
  };
  const handleRemove = () => {
    setImgBase64('');
    setImgFile(null);
  };
  return (
    <>
      <div className="input_item">
        <p className="input_title">이미지</p>
      </div>
      <div className="img_upload">
        <div className="img_preview">
          {imgBase64 ? (
            <img
              src={imgBase64}
              onClick={handleRemove}
              style={{ width: '400px' }}
              alt="smallimagein"
            />
          ) : (
            <img
              src={'/defaultimage.png'}
              style={{ width: '400px' }}
              alt="smallimage"
            />
          )}
        </div>
        <div className="img_add">
          <p onChange={handleChangeFile}>이미지 추가</p>
          <input
            type="file"
            name="imgFile"
            id="ex_file"
            className="ex_file"
            onChange={handleChangeFile}
          />
        </div>
      </div>
    </>
  );
};
