import React from 'react';
import 'tui-image-editor/dist/tui-image-editor.css';
import ImageEditor from '@toast-ui/react-image-editor';
import iconA from 'tui-image-editor/dist/svg/icon-a.svg';
import iconB from 'tui-image-editor/dist/svg/icon-b.svg';
import iconC from 'tui-image-editor/dist/svg/icon-c.svg';
import startImg from './img/sampleImage.jpg';
// import './PaintingPage.css';
// import './Page.css';
import { SmallHeader } from '../../components/common/SmallHeader';

var customTheme = {
  'common.bi.image':
    'https://uicdn.toast.com/toastui/img/tui-image-editor-bi.png',
  'common.bisize.width': '251px',
  'common.bisize.height': '21px',
  'common.backgroundImage': 'none',
  'common.backgroundColor': '#1e1e1e',
  'common.border': '0px',

  // header
  'header.backgroundImage': 'none',
  'header.backgroundColor': 'transparent',
  'header.border': '0px',

  // load button
  'loadButton.backgroundColor': '#fff',
  'loadButton.border': '1px solid #ddd',
  'loadButton.color': '#222',
  'loadButton.fontFamily': 'NotoSans, sans-serif',
  'loadButton.fontSize': '12px',

  // download button
  'downloadButton.backgroundColor': '#fdba3b',
  'downloadButton.border': '1px solid #fdba3b',
  'downloadButton.color': '#fff',
  'downloadButton.fontFamily': 'NotoSans, sans-serif',
  'downloadButton.fontSize': '12px',

  // main icons
  'menu.normalIcon.path': iconB,
  'menu.normalIcon.name': 'icon-b',
  'menu.activeIcon.path': iconA,
  'menu.activeIcon.name': 'icon-a',
  'menu.iconSize.width': '24px',
  'menu.iconSize.height': '24px',
  'menu.disabledIcon.path': iconA,
  'menu.hoverIcon.path': iconC,

  // submenu primary color
  'submenu.backgroundColor': '#1e1e1e',
  'submenu.partition.color': '#858585',

  // submenu icons
  'submenu.normalIcon.path': iconA,
  'submenu.normalIcon.name': 'icon-a',
  'submenu.activeIcon.path': iconC,
  'submenu.activeIcon.name': 'icon-c',
  'submenu.iconSize.width': '32px',
  'submenu.iconSize.height': '32px',

  // submenu labels
  'submenu.normalLabel.color': '#858585',
  'submenu.normalLabel.fontWeight': 'lighter',
  'submenu.activeLabel.color': '#fff',
  'submenu.activeLabel.fontWeight': 'lighter',

  // checkbox style
  'checkbox.border': '1px solid #ccc',
  'checkbox.backgroundColor': '#fff',

  // rango style
  'range.pointer.color': '#fff',
  'range.bar.color': '#666',
  'range.subbar.color': '#d1d1d1',
  'range.value.color': '#fff',
  'range.value.fontWeight': 'lighter',
  'range.value.fontSize': '11px',
  'range.value.border': '1px solid #353535',
  'range.value.backgroundColor': '#151515',
  'range.title.color': '#fff',
  'range.title.fontWeight': 'lighter',

  // colorpicker style
  'colorpicker.button.border': '1px solid #1e1e1e',
  'colorpicker.title.color': '#fff',
};

const PaintingForm = () => (
  <ImageEditor
    includeUI={{
      loadImage: {
        path: startImg,
        name: 'SampleImage',
      },
      theme: customTheme,
      menu: [
        'crop',
        'flip',
        'rotate',
        'draw',
        'shape',
        'icon',
        'text',
        'mask',
        'filter',
      ],
      initMenu: 'draw',
      uiSize: {
        width: '700px',
        height: '900px',
      },
      menuBarPosition: 'bottom',
    }}
    cssMaxHeight={500}
    cssMaxWidth={700}
    selectionStyle={{
      cornerSize: 20,
      rotatingPointOffset: 70,
      cornerColor: 'blue',
      cornerStrokeColor: '#00a9ff',
      borderColor: '#00a9ff',
    }}
    usageStatistics={true}
  />
);

const PaintingContainer = () => {
  return (
    <div>
      <SmallHeader>
        <h1 className="title">그림 그리기</h1>
        <hr />
        <h1 className="description">
          새로운 그림을 색칠해 보세요. 다양한 도구로 색칠할 수 있습니다.
        </h1>
      </SmallHeader>
      <div>
        <PaintingForm />
      </div>
    </div>
  );
};

export default PaintingContainer;
