import { createAction, handleActions } from 'redux-actions';
import createRequestSaga, {
  createRequestActionTypes,
} from '../lib/createRequestSaga';
import * as authAPI from '../lib/api/auth';
import { takeLatest } from 'redux-saga/effects';

const INITIALIZE = 'guestbook/INITIALIZE';
const CHANGE_FIELD = 'guestbook/CHANGE_FIELD';

const [
  CREATE_GUESTBOOK,
  CREATE_GUESTBOOK_SUCCESS,
  CREATE_GUESTBOOK_FAILURE,
] = createRequestActionTypes('guestbook/CREATE_GUESTBOOK');

export const initialize = createAction(INITIALIZE);
export const changeGuestbook = createAction(
  CHANGE_FIELD,
  ({ commenter, name, text, profileid }) => ({
    commenter,
    name,
    text,
    profileid,
  }),
);
export const createGuestbook = createAction(
  CREATE_GUESTBOOK,
  ({ commenter, name, text, profileid }) => ({
    commenter,
    name,
    text,
    profileid,
  }),
);

const createGuestbookSaga = createRequestSaga(
  CREATE_GUESTBOOK,
  authAPI.createGuestbook,
);
export function* guestbookSaga() {
  yield takeLatest(CREATE_GUESTBOOK, createGuestbookSaga);
}

const initialState = {
  commenter: '',
  name: '',
  profileid: '',
  text: '',
  comment: null,
  commentError: null,
};

const guestbook = handleActions(
  {
    [INITIALIZE]: state => initialState,
    [CHANGE_FIELD]: (
      state,
      { payload: { commenter, name, text, profileid } },
    ) => ({
      ...state,
      commenter: commenter,
      profileid: profileid,
      text: text,
      name: name,
    }),
    [CREATE_GUESTBOOK]: state => ({
      ...state,
      post: null,
      postError: null,
    }),
    [CREATE_GUESTBOOK_SUCCESS]: (state, { payload: post }) => ({
      ...state,
      post,
    }),
    [CREATE_GUESTBOOK_FAILURE]: (state, { payload: postError }) => ({
      ...state,
      postError,
    }),
  },
  initialState,
);

export default guestbook;
