import React, { useEffect, useCallback } from 'react';
import Editor from '../../components/writecategory/Editor';

import { useSelector, useDispatch } from 'react-redux';
import { changeField, initialize } from '../../modules/write';
import { SmallHeader } from '../../components/common/SmallHeader';

const EditorContainer = () => {
  const dispatch = useDispatch();
  const { title, content, postError } = useSelector(({ write }) => ({
    title: write.title,
    content: write.content,
    postError: write.postError,
  }));

  const onChangeField = useCallback(
    (payload) => dispatch(changeField(payload)),
    [dispatch],
  );

  useEffect(() => {
    return () => {
      dispatch(initialize());
    };
  }, [dispatch]);

  const current_user = localStorage.getItem('current_user');
  if (current_user) {
    return (
      <>
        <SmallHeader>
          <h1 className="title">글쓰기</h1>
          <hr />
          <h1 className="description">
            다양한 도구로 그린 그림을 전시할 수 있습니다.
          </h1>
        </SmallHeader>
        <Editor onChangeField={onChangeField} title={title} content={content} />
      </>
    );
  } else {
    return <>{postError}</>;
  }
};

export default EditorContainer;
