import React, { useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { rankPosts } from '../../modules/rank';
import Ranktable from '../../components/rank/Ranktable';
import { SmallHeader } from '../../components/common/SmallHeader';

const RankContainer = () => {
  const dispatch = useDispatch();
  const { rank } = useSelector(({ rank }) => ({
    rank: rank.rank,
  }));

  useEffect(() => {
    dispatch(rankPosts());
  }, [dispatch]);

  if (!rank) {
    return <></>;
  }
  return (
    <>
      <SmallHeader>
        <h1 className="title">랭킹</h1>
        <hr />
        <h1 className="description">
          많은 좋아요를 받은 랭킹입니다. 여러분도 도전해 보세요
        </h1>
      </SmallHeader>
      <Ranktable rank={rank} />
    </>
  );
};

export default withRouter(RankContainer);
