import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import { Link } from 'react-router-dom';
import LinearProgress from '@material-ui/core/LinearProgress';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

export default function Ranktable({ rank }) {
  const classes = useStyles();
  if (rank.results) {
    const ranking = rank.results;
    const count = rank.count;
    return (
      <>
        <TableContainer component={Paper}>
          <Table className={classes.table} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>순위</TableCell>
                <TableCell align="right">아이콘</TableCell>
                <TableCell align="right">닉네임</TableCell>
                <TableCell align="right">백분위(상위)</TableCell>
                <TableCell align="right">백분위(상위)</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {ranking.map((item, index) => (
                <TableRow key={index}>
                  <TableCell component="th" scope="row">
                    {index + 1}
                  </TableCell>
                  <TableCell align="right">
                    <Link to={`/profile/${item.user}`}>
                      <Avatar src={item.user_image} />
                    </Link>
                  </TableCell>
                  <TableCell align="right">{item.nickname}</TableCell>
                  <TableCell align="right">
                    {Math.round(((index + 1) / count) * 100)}%
                  </TableCell>
                  <TableCell align="right">
                    <LinearProgress
                      variant="buffer"
                      valueBuffer={Math.round(((index + 1) / count) * 100)}
                      value={Math.round(((index + 1) / count) * 100)}
                      color="secondary"
                    />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </>
    );
  } else {
    return <></>;
  }
}
