import React, { useEffect, useState, useCallback } from 'react';
import { removeComment } from '../../lib/api/posts';
import Avatar from '@material-ui/core/Avatar';
import Grid from '@material-ui/core/Grid';
import client from '../../lib/api/client';
import { AiOutlineCloseCircle } from 'react-icons/ai';

const CommentItem = ({
  commenterId,
  name,
  text,
  commentId,
  onDelete,
  created_at,
  comment,
  setCommentall,
  postid,
}) => {
  // const getCommentall = (postid) => {
  //   client
  //     .get(`/api/posts/${postid}`)
  //     .then((res) => setCommentall(res.data.comments))
  //     .catch((e) => window.location.reload());
  // };
  const onRemove = useCallback(async () => {
    const getCommentall = (postid) => {
      client
        .get(`/api/posts/${postid}`)
        .then((res) => setCommentall(res.data.comments))
        .catch((e) => window.location.reload());
    };
    // await getCommentall(postid);
    await removeComment(commentId);
    await getCommentall(postid);
  }, [postid, commentId, setCommentall]);
  const [owner, setOwner] = useState('');

  useEffect(() => {
    const getOwner = (commenterId) => {
      client
        .get(`/api/profile/${commenterId}`)
        .then((res) => setOwner(res.data))
        .catch((e) => window.location.reload());
    };
    getOwner(commenterId);
  }, [commenterId]);
  const current_user = localStorage.getItem('current_user');
  return (
    <>
      <Grid
        container
        style={{
          padding: '20px',
        }}
      >
        <Grid
          item
          xs={12}
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            borderTop: 'solid 2px white',
          }}
        >
          <div style={{ display: 'inline-block' }}>
            <div
              style={{
                display: 'inline-block',
                verticalAlign: 'middle',
                marginRight: '20px',
              }}
            >
              <Avatar src={owner.user_image} />
            </div>
            <div
              style={{
                color: 'white',
                display: 'inline-block',
                verticalAlign: 'middle',
                marginRight: '20px',
              }}
            >
              <h4>{owner.nickname}</h4>
            </div>
            <div
              style={{
                display: 'inline-block',
                verticalAlign: 'middle',
                marginRight: '20px',
              }}
            >
              <h4>{created_at.slice(0, 16).replace('T', ' ')}</h4>
            </div>
          </div>

          <div style={{ color: 'white', display: 'inline-block' }}>
            {current_user === name ? (
              <span onClick={onRemove}>
                <AiOutlineCloseCircle
                  onClick={onDelete}
                  style={{ fontSize: '20px' }}
                />
              </span>
            ) : (
              ''
            )}
          </div>
        </Grid>

        <h4> {text}</h4>
      </Grid>
    </>
  );
};

export default CommentItem;
