import React, { useState, useCallback, useEffect } from 'react';
import client from '../../lib/api/client';

import Button from '@material-ui/core/Button';
import FollowPopover from './FollowPopover';

const FollowButton = ({ own_follower, own_following, own_id }) => {
  const [mine, setMine] = useState('');
  const my_id = parseInt(localStorage.getItem('user_id'));
  const onClick = useCallback(() => {
    const getMine = (my_id) => {
      client
        .get(`/api/profile/${my_id}`)
        .then((res) => setMine(res.data))
        .catch((e) => {
          return e;
        });
    };
    getMine(my_id);
  }, [my_id]);
  useEffect(() => {
    const getMine = (my_id) => {
      client
        .get(`/api/profile/${my_id}`)
        .then((res) => setMine(res.data))
        .catch((e) => {
          return e;
        });
    };
    getMine(my_id);
  }, [my_id]);
  const my_following = mine.following;
  const num_follower = own_follower.length;
  const num_following = own_following.length;

  const onRemove = async () => {
    await my_following.splice(my_following.indexOf(own_id), 1);
    // 상대 followers에 user(내아이디) 뺌
    await own_follower.splice(own_follower.indexOf(my_id), 1);
    // console.log(my_follower);
    // console.log(own_follower);
    await client
      .patch(`/api/profile/${my_id}/`, {
        following: my_following,
      })
      .then((res) => {
        return res;
      });

    await client
      .patch(`/api/profile/${own_id}/`, {
        follower: own_follower,
      })
      .then((res) => {
        return res;
      });
  };
  const onLike = async () => {
    // 내 following에 ownerid(상대방 아이디) 넣음
    await my_following.push(parseInt(own_id));
    // 상대 followers에 user(내아이디) 넣음
    await own_follower.push(my_id);
    // console.log(my_follower);
    // console.log(own_follower);

    await client
      .patch(`/api/profile/${my_id}/`, {
        following: my_following,
      })
      .then((res) => {
        return res;
      });

    await client
      .patch(`/api/profile/${own_id}/`, {
        follower: own_follower,
      })
      .then((res) => {
        return res;
      });
  };

  // 상대 팔로워에 나 없으면, 내가 자신이 아니면
  if (my_following && own_id !== my_id) {
    return (
      <>
        <Button onClick={onClick}>
          <h4>
            <FollowPopover
              type={'팔로워'}
              num={num_follower}
              follow={own_follower}
            />
          </h4>
          <h4>
            <FollowPopover
              type={'팔로잉'}
              num={num_following}
              follow={own_following}
            />
          </h4>
          {my_following.indexOf(own_id) === -1 ? (
            <Button onClick={onLike} variant="outlined" color="primary">
              팔로우
            </Button>
          ) : (
            <Button onClick={onRemove} variant="outlined" color="primary">
              팔로우 취소
            </Button>
          )}
        </Button>
      </>
    );
  } else {
    return <></>;
  }
};

export default FollowButton;
